@font-face {
  font-family: "LatoLight";
  font-weight: normal;
  font-style: normal;
  src: url("./assets/fonts/LatoLatin-Light.woff2") format("woff2"),
    url("./assets/fonts/LatoLatin-Light.woff") format("woff"),
    url("./assets/fonts/LatoLatin-Light.ttf") format("truetype");
}

@font-face {
  font-family: "LatoRegular";
  font-weight: normal;
  font-style: normal;
  src: url("./assets/fonts/LatoLatin-Regular.woff2") format("woff2"),
    url("./assets/fonts/LatoLatin-Regular.woff") format("woff"),
    url("./assets/fonts/LatoLatin-Regular.ttf") format("truetype");
}

* {
  box-sizing: border-box;
}

body {
  margin: 0;
  font-family: "LatoRegular", sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}
